import { RemoveByDeath, theStage, GetCurrentUnit } from "../Database";
import $ from "jquery";
var Enemy = (function () {
    function Enemy(baseHP, baseDamage) {
        this.baseHP = baseHP;
        this.maxHP = this.baseHP * theStage.Level;
        this.currentHP = this.MaxHP;
        this.baseDamage = baseDamage;
        this.currentDamage = this.baseDamage * theStage.Level;
    }
    Enemy.prototype.UpdateFeedback = function (counter) {
        if (counter % 20) {
            this.Hurt();
            this.Regenerate(10 * theStage.Level);
        }
    };
    Object.defineProperty(Enemy.prototype, "MaxHP", {
        get: function () {
            this.maxHP = this.baseHP * theStage.Level;
            return this.maxHP;
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(Enemy.prototype, "CurrentHP", {
        get: function () {
            return this.currentHP;
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(Enemy.prototype, "CurrentDamage", {
        get: function () {
            this.currentDamage = this.baseDamage * theStage.Level;
            return this.currentDamage;
        },
        enumerable: true,
        configurable: true
    });
    Enemy.prototype.ReceiveDamage = function (damage) {
        this.currentHP -= damage;
        this.currentHP = Math.max(this.currentHP, 0);
        adjustBarAnimation("monster-hp", (this.CurrentHP / this.MaxHP));
        if (this.currentHP == 0) {
            this.Die();
        }
    };
    Enemy.prototype.Birth = function () {
        adjustBarAnimation("monster-hp", this.MaxHP);
    };
    Enemy.prototype.Die = function () {
        RemoveByDeath("Enemy");
    };
    Enemy.prototype.Hurt = function () {
        GetCurrentUnit().ReceiveDamage(this.CurrentDamage);
    };
    Enemy.prototype.Regenerate = function (health) {
        this.currentHP += health;
        this.currentHP = Math.min(this.CurrentHP, this.MaxHP);
        adjustBarAnimation("monster-hp", (this.CurrentHP / this.MaxHP));
    };
    return Enemy;
}());
export { Enemy };
function adjustBarAnimation(type, percentage) {
    $("#" + type + "-bar").animate({ "width": ("" + percentage.toString() + "%") }, 200);
    if (percentage == 100) {
        $("#" + type + "-bar").animate({ "width": ("0%") }, 100);
    }
}
